.menu {
  display: flex;
  min-width: 1120px;
  flex-wrap: nowrap;
  padding-left: 20px;
  justify-content: space-around;
  list-style-type: none;
  align-items: center; }
  @media (min-width: 768px) {
    .menu {
      display: flex;
      min-width: 1120px;
      flex-wrap: nowrap;
      margin: 0;
      padding-left: 20px;
      justify-content: space-around;
      list-style-type: none;
      align-items: center; } }
  @media (min-width: 1440px) {
    .menu {
      display: flex;
      width: 1260px;
      flex-wrap: wrap;
      margin-left: 40px;
      padding-left: 40px;
      justify-content: space-around;
      list-style-type: none;
      margin-left: auto;
      margin-right: auto;
      align-items: center;
      padding-left: 10px; } }

.menu__item {
  text-align: center;
  height: 40px;
  line-height: 36px;
  font-size: 16px;
  color: #7e7e82; }

@media (min-width: 1440px) {
  .menu__item {
    flex: 0 1 246px; }
  .menu__item:nth-child(5) {
    position: relative;
    right: 15px; }
  .menu__item:hover {
    border: 2px solid #41f6d7;
    background-color: #fff; } }

.menu__item--aqua {
  flex: 0 1 246px;
  padding: 0;
  border: solid 2px #b8ffec;
  background-color: #fff; }

.menu__item--new-line {
  margin-right: 0; }

@media (min-width: 1440px) {
  .menu__item--new-line {
    margin-right: auto; }
    .menu__item--new-line a {
      display: flex;
      margin-left: 32px; }
  .menu__item:hover {
    border: 2px solid #41f6d7;
    background-color: #fff; } }

.menu__item:hover {
  border: 2px solid #41f6d7;
  background-color: #fff; }

@media (max-width: 1440px) {
  .menu__item:hover {
    border: 2px solid #41f6d7;
    background-color: #fff;
    padding: 0 15px 5px 15px; } }
